import api from "../api";

const env = process.env;
const api_base = env.api_base;

class PhantomAuth {
    signIn(username, password) {
        return api(api_base + 'auth').post({
            data: {
                attributes: {
                    username: username,
                    password: password
                },
            },
        }).catch(error => { throw { message: error.error } })
            .then(response => {
                return { ...response, token: response.signInUserSession.token }
            });
    }

    async register(username, password, name) {
        let response;
        try {
            response = await api(api_base + 'register').post({
                data: {
                    attributes: {
                        name,
                        username,
                        password
                    },
                },
            });
        } catch (error) {
            throw { message: error.error };
        }
        return { ...response, token: response.signInUserSession.token };
    }

    async verification_email(email) {
        let response;
        try {
            response = await api(api_base + 'verification_email').post({
                data: {
                    attributes: {
                        email
                    },
                },
            });
        } catch (error) {
            throw { message: error.error };
        }
        return { ...response };
    }

    async verify_email(email, code) {
        let response;
        try {
            response = await api(api_base + 'verification_email').put(code, {
                data: {
                    attributes: {
                        email
                    },
                },
            });
        } catch (error) {
            throw { message: error.error };
        }
        return { ...response, token: response.signInUserSession.token };
    }
}

export default {
    Auth: null,
    init() {
        try {
            this.Auth = new PhantomAuth();
        } catch (error) {
            console.error(error);
        }
    },
};
