<script>
	import { user } from "../store";
	import { onMount } from "svelte";
	import { push } from "svelte-spa-router";
	import Auth from "../Auth";
	import { translations as _ } from "../helpers";

	let name = "";
	let username = "";
	let password = "";

	const auth = new Auth();

	onMount(async () => {
		document.querySelector('input[name="email"]').focus();
	});

	async function onSubmit() {
		try {
			const response = await auth.register(username, password, name);
			user.update(() => response);
			push("#/verify_email");
		} catch (error) {
			window.alert(error.message);
		}
	}
</script>

<main>
	<form on:submit|preventDefault={onSubmit}>
		<h3>{_("Register")}</h3>
		<p>{_("Please register your email and password")}</p>
		<input
			bind:value={username}
			name="email"
			aria-label="email"
			placeholder={_("Email")}
			type="email"
			autocorrect="off"
			autocapitalize="none"
		/>
		<input
			bind:value={name}
			name="name"
			aria-label="name"
			placeholder={_("Name")}
			type="text"
			autocorrect="off"
			autocapitalize="none"
		/>
		<input
			bind:value={password}
			name="password"
			aria-label="password"
			placeholder={_("Password")}
			type="password"
		/>
		<button type="submit">{_("Register")}</button>
	</form>
</main>

<style scoped>
	main {
		background-image: url("../images/login_bg.jpg");
		background-size: cover;
		width: 100vw;
		height: 100vh;
	}
	form {
		max-width: 20rem;
	}
	form input {
		width: 100%;
	}
</style>
