<script>
	import { user } from "../store";
	import { push, querystring } from "svelte-spa-router";
	import Auth from "../Auth";
	import { translations as _ } from "../helpers";

	let email = "";
	let code;

	const auth = new Auth();

    user.subscribe((user) => {
		email = user.attributes.email;
    });
	$: {
		code = new URLSearchParams($querystring).get("code");
		if (email && code) {
			verifyEmail(email, code);
		}
	}
	async function resend() {
		try {
			await auth.verification_email(email);
		} catch (error) {
			window.alert(error.message);
		}
	}
	async function verifyEmail(email, code) {
		try {
			const response = await auth.verify_email(email, code);
			user.update(() => response);
			push("#/dashboard");
		} catch (error) {
			window.alert(error.message);
		}
	}
</script>

<main>
	{#if code}
	<form>
		<i class="fas fa-spinner fa-spin"></i> {_("Verifying email...")}
	</form>
	{:else}
	<form on:submit|preventDefault={resend}>
		<h3>{_("Email verification")}</h3>
		<p>{_("An email has been sent to you. Please click on the link to verify your email address.")}</p>
		<button type="submit">{_("Resend verification email")}</button>
	</form>
	{/if}
</main>

<style scoped>
	main {
		background-image: url("../images/login_bg.jpg");
		background-size: cover;
		width: 100vw;
		height: 100vh;
	}
	form {
		max-width: 20rem;
	}
</style>
