<script lang="ts">
	import * as examConfig from "../../microservices/models/exams.json";
	import api from "../api";
	import { user, answers } from "../store";
	import dayjs from "dayjs";

	export let params = {
		id: null,
	};

	// initialize exam with empty values
	const attrs = Object.keys(examConfig.attributes);
	const form = JSON.parse(JSON.stringify(examConfig));
	form.ui = Object.keys(form.ui).reduce((res, key) => {
		if (form.ui[key].showInUpdate !== false) {
			res[key] = form.ui[key];
		}
		return res;
	}, {});
	const attributes = attrs.reduce(
		(acc, key) => {
			acc[key] = "";
			return acc;
		},
		{
			user_id: 0,
			exam_id: 0,
			title: "",
			subtitle: "",
			questions: [],
			created_at: 0,
			max_time: 0,
			time: 0,
		}
	);
	let exam = {
		attributes,
	};
	let loggedUser;
	let questions = [];
	let currentQuestion = {
		attributes: {
			question_html: "",
			option_1_html: "",
			option_2_html: "",
			option_3_html: "",
			option_4_html: "",
		},
		options: [],
	};
	let currentQuestionIndex = -1;
	let responses = [];
	let completed = false;
	let results = [];
	// get stores answers
	answers.subscribe((value) => {
		responses = value || [];
	});
	// get logged user
	user.subscribe((value) => {
		loggedUser = value;
	});
	// get exam
	api("user_exam_tries")
		.get(params.id)
		.then((data) => {
			exam = data;
			exam.attributes.questions.forEach(async (question, index) => {
				const data = await api("questions").get(question, {
					params: { exam_id: exam.attributes.exam_id },
				});
				questions[index] = data;
				questions = questions;
			});
		});
	function setCurrentQuestion(questionIndex) {
		currentQuestionIndex = questionIndex;
		const question = questions[questionIndex];
		if (!question || !question.attributes) {
			return;
		}
		// shuffle options
		const options = [
			{ index: 1, html: question.attributes.option_1_html },
			{ index: 2, html: question.attributes.option_2_html },
			{ index: 3, html: question.attributes.option_3_html },
			{ index: 4, html: question.attributes.option_4_html },
		];
		const shuffledOptions = options.sort(() => 0.5 - Math.random());
		currentQuestion = question;
		currentQuestion.options = shuffledOptions;
	}
	function selectOption(option) {
		answers.update((value) => {
			value = value || [];
			value[currentQuestionIndex] = option;
			return value;
		});
		if (currentQuestionIndex < questions.length - 1) {
			setCurrentQuestion(currentQuestionIndex + 1);
		} else {
			sendAnswers();
		}
	}
	function sendAnswers() {
		api("user_exam_tries")
			.put(params.id, {
				data: {
					attributes: {
						exam_id: exam.attributes.exam_id,
						answers: responses,
						time: Math.floor(Date.now() / 1000),
					},
				},
			})
			.then((data) => {
				completed = true;
				results = data.attributes.results;
			});
	}
	$: {
		completed =
			questions.length <= responses.length && questions.length > 0;
		if (questions.length > 0) {
			setCurrentQuestion(responses.length);
		}
	}
</script>

<main>
	<div class="content">
		<div class="flex">
			<h3>{exam.attributes.title}</h3>
			<div class="clock">
				{dayjs(
					exam.attributes.created_at * 1000 +
						exam.attributes.max_time * 60  * 1000
				).diff(dayjs(), "minute")} min
			</div>
		</div>
		{#if currentQuestion && !completed}
			<p>
				{@html currentQuestion.attributes.question_html}
			</p>
			{#each currentQuestion.options as option}
				<button
					class="answer"
					type="button"
					on:click={() => selectOption(option.index)}
				>
					{@html option.html}
				</button>
			{/each}
		{:else if completed}
			{#each results as result}
				<div class={`result ${result.correct ? 'correct' : 'incorrect'}`}>
					<div>
						<p><b>{result.number}.&nbsp;</b></p>
					</div>
					<div>
						<b>{@html result.question}</b>
					</div>
					{#if result.correct}
						<div><p><i class="fas fa-check" /></p></div>
						<div>
							<p>{@html result.answer}</p>
						</div>
					{:else}
						<div><p><i class="fas fa-times" /></p></div>
						<div>
							<p>{@html result.answer}</p>
							<hr>
							<b>Repasa:</b>
							{@html result.reference.replace(/<a /g, "<a target='_blank' ")}
						</div>
					{/if}
				</div>
			{/each}
		{/if}
	</div>
</main>
