import aws_amplify from './auth/amplify.js';
import phantom from './auth/phantom.js';
const env = process.env;
class Auth {
    // constructor
    constructor() {
        if (env.auth_driver === 'amplify') {
            aws_amplify.init();
            this.driver = aws_amplify.Auth;
        }
        else {
            phantom.init();
            this.driver = phantom.Auth;
        }
    }
    signIn(username, password) {
        return this.driver.signIn(username, password);
    }
    register(username, password, name) {
        return this.driver.register(username, password, name);
    }
    verification_email(email) {
        return this.driver.verification_email(email);
    }
    verify_email(email, code) {
        return this.driver.verify_email(email, code);
    }
}
export default Auth;
