<script lang="ts">
	/** Pages **/
	import Router from "svelte-spa-router";
	import Alert from "./components/Alert.svelte";
	import Login from "./pages/Login.svelte";
	import Home from "./pages/Home.svelte";
	import Test from "./pages/Test.svelte";
	import Dashboard from "./pages/Dashboard.svelte";
	import AdminProfile from "./pages/AdminProfile.svelte";
	import Cajas from "./pages/Cajas.svelte";
	import Creditos from "./pages/Creditos.svelte";
	import Clientes from "./pages/Clientes.svelte";
	import AdminUsers from "./pages/AdminUsers.svelte";
	import PlanAnual from "./pages/PlanAnual.svelte";
	import Movimientos from "./pages/Movimientos.svelte";
	import Topbar from "./components/Topbar.svelte";
	import Menu from "./components/Menu.svelte";
	import Tooltip from "./components/Tooltip.svelte";
	import Resultados from "./pages/Resultados.svelte";
	import Register from "./pages/Register.svelte";
	import VerifyEmail from "./pages/VerifyEmail.svelte";
	import Exams from "./pages/Exams.svelte";
	import Questions from "./pages/Questions.svelte";
	import Exam from "./pages/Exam.svelte";
	import ExamTry from "./pages/ExamTry.svelte";
	import PagoQr from "./pages/PagoQR.svelte";
	import Products from "./pages/Products.svelte";

	let currentPage = "";

	const routes = {
		"/": Login,
		"/home": Home,
		"/test": Test,
		"/dashboard": Dashboard,
		"/admin/profile": AdminProfile,
		"/admin/users": AdminUsers,
		"/cajas": Cajas,
		"/creditos": Creditos,
		"/clientes": Clientes,
		"/plan_anual": PlanAnual,
		"/actividades/:id/resultados": Resultados,
		"/exams": Exams,
		"/exams/:id/questions": Questions,
		"/exam/:id": Exam,
		"/exam/try/:id": ExamTry,
		"/informe/:id/movimientos": Movimientos,
		"/register": Register,
		"/verify_email": VerifyEmail,
		"/pago/:code/:exam?": PagoQr,
		"/products": Products,
	};
	function routeLoaded(event) {
		currentPage = event.detail.location;
	}
</script>

<Alert />
{#if currentPage && currentPage !== "/" && currentPage !== "/register" && currentPage !== "/verify_email"}
	<Topbar />
	<Menu />
{/if}

<Router {routes} on:routeLoaded={routeLoaded} />
<Tooltip />
