<script lang="ts">
	import * as examConfig from "../../microservices/models/exams.json";
	import { push } from "svelte-spa-router";
	import api from "../api";
	import { user, answers } from "../store";

	export let params = {
		id: null,
	};

	// initialize exam with empty values
	const attrs = Object.keys(examConfig.attributes);
	const form = JSON.parse(JSON.stringify(examConfig));
	form.ui = Object.keys(form.ui).reduce((res, key) => {
		if (form.ui[key].showInUpdate !== false) {
			res[key] = form.ui[key];
		}
		return res;
	}, {});
	const attributes = attrs.reduce(
		(acc, key) => {
			acc[key] = "";
			return acc;
		},
		{
			title: "",
			subtitle: "",
			questions: 0,
			total_questions: 0,
			time: 0,
			price: 0,
			price_credits: 1,
			purchased: null,
			tries: 0,
			approvals: 0,
		}
	);
	let exam = {
		attributes,
	};
	// get logged user
	let loggedUser;
	user.subscribe((value) => {
		loggedUser = value;
	});
	// get exam
	api("exams")
		.get(params.id)
		.then((data) => {
			exam = data;
		});
	function refresh() {
		api("exams")
			.get(params.id)
			.then((data) => {
				exam = data;
			});
	}
	function comprar() {
		push(`/pago/c0m3x4/${params.id}`);
	}
	function tryExam() {
		api("user_exam_tries")
			.post({
				data: {
					attributes: {
						exam_id: params.id,
						user_id: loggedUser.attributes.id,
						title: exam.attributes.title,
						subtitle: exam.attributes.subtitle,
						max_time: exam.attributes.time,
					},
				},
			})
			.then((examTry) => {
				answers.update(() => {
					return [];
				});
				push("/exam/try/" + examTry.id);
			});
	}
</script>

<main>
	<form>
		<h2>{exam.attributes.title}</h2>
		<p>{exam.attributes.subtitle}</p>
		<p>
			<b>Número de preguntas:</b>
			{exam.attributes.questions}
		</p>
		<p>
			<b>Tiempo aproximado:</b>
			{exam.attributes.time} minutos
		</p>
		<hr />
		<p>
			<b>Banco de preguntas:</b>
			{exam.attributes.total_questions}
		</p>
		{#if !exam.attributes.purchased}
			<p>
				<span class="price">
					<b>Precio:&nbsp;</b>
					{#if exam.attributes.price > 0}
						<span>
							{Number(exam.attributes.price).toFixed(2)} Bs.<br />
							ó <a href="#/admin/profile">sucribete a la plataforma</a>
						</span>
						{#if !exam.attributes.purchased}
							<div class="price-button">
								<button on:click={comprar}>Comprar</button>
							</div>
						{/if}
					{:else}
						<span> Gratis </span>
						{#if !exam.attributes.purchased}
							<div class="price-button">
								<button on:click={comprar}>Agregar</button>
							</div>
						{/if}
					{/if}
				</span>
			</p>
		{/if}
	</form>
	{#if exam.attributes.purchased}
		<form on:submit|preventDefault={tryExam}>
			<p><b>Intentos realizados:</b> {exam.attributes.tries}</p>
			<p><b>Aprobados:</b> {exam.attributes.tries}</p>
			<button type="submit">
				PROBAR EXAMEN &nbsp;&nbsp;
				<i class="fas fa-edit" />
			</button>
		</form>
	{/if}
</main>

<style>
	.price {
		display: flex;
		flex-direction: row;
		justify-content: start;
		align-items: flex-start;
	}
	.price .price-button {
		flex-grow: 1;
		text-align: right;
	}
</style>
