<script>
	import api from "../api";

	import Api from "../components/Api.svelte";
	import Avatar from "../components/Avatar.svelte";
	import GridTemplate from "../components/GridTemplate.svelte";
	import InputFile from "../components/InputFile.svelte";
	import { user } from "../store";
	export let params = {
		code: null,
		exam: null,
	};
	let userLogged = {};
	let comprobante = "";
	let wm_link = window.location.hash;
	let product = {
		attributes: {
			name: "",
			code: "",
			qr_code: "",
			account_name: "",
		},
	};
	const env = process.env;
	user.subscribe(function (newValue) {
		userLogged = newValue;
	});
	api("products")
		.get(null, { filter: [`code(${JSON.stringify(params.code)})`] })
		.then((response) => {
			product = response[0];
			paypal
				.Buttons({
					style: {
						shape: "rect",
						color: "white",
						layout: "vertical",
						label: "subscribe",
					},
					createSubscription: function (data, actions) {
						return actions.subscription.create({
							/* Creates the subscription */
							plan_id: "P-3GE01804BY634224PMKHA2NQ",
						});
					},
					onApprove: function (data, actions) {
						alert(data.subscriptionID); // You can add optional success message for the subscriber here
					},
				})
				.render("#paypal-button-container-P-3GE01804BY634224PMKHA2NQ"); // Renders the PayPal button
		});
	function whatsapp_link(message) {
		const isMobile = /iPhone|iPad|iPod|Android/i.test(navigator.userAgent);
		return `${env.api_base}wm?${
			isMobile ? "mb&" : ""
		}text=${encodeURIComponent(message)}`;
	}
	$: {
		const msg = `Hola soy ${userLogged.attributes.name}\n✉️ ${userLogged.attributes.email}\nRealize el pago por concepto de: *${product.attributes.name}*.\nAdjunto la imagen del deposito: \n\n${comprobante}`;
		wm_link = whatsapp_link(msg);
	}
</script>

<main>
	<form>
		<div id="paypal-button-container-P-3GE01804BY634224PMKHA2NQ" />
	</form>
	{#if product.attributes.name}
		<form>
			<h2>PAGO POR QR</h2>
			<a
				class="button"
				href={product.attributes.qr_code}
				target="_blank"
				download
			>
				<img src={product.attributes.qr_code} alt="logo" class="QR" />
			</a>
			<div>
				<small>
					{product.attributes.price} a {product.attributes
						.account_name} por concepto de
					<b>{product.attributes.name}</b>
				</small>
			</div>
			<div>
				<a
					class="button"
					href={product.attributes.qr_code}
					target="_blank"
					download
				>
					Descargar QR
				</a>
			</div>
			<p>
				En cuanto realice el pago, por favor suba la imagen del
				comprobante y enviela.
			</p>
			<InputFile
				class="w-100"
				value={comprobante}
				label="Subir comprobante"
				showValue={false}
				on:input={(event) => {
					comprobante = (event.target || event.detail).value;
				}}
			/>
			<a
				class={`button ${comprobante ? "" : "disabled"}`}
				href={wm_link}
				target="_blank"
			>
				ENVIAR
			</a>
		</form>
	{/if}
</main>
