<script>
	import { createEventDispatcher } from "svelte";
	import actions from "../helpers/actions";

	export let value;
	export let name = "";
	export let config = {};
	const dispatch = createEventDispatcher();

	let textarea;
	let preview, gg;
	function open() {
		preview.innerHTML = '';
		dispatch("hide-popup");
		const el = textarea;
		const stackedit = new window.Stackedit();

		// Open the iframe
		stackedit.openFile({
			name: name, // with an optional filename
			content: {
				text: el.value, // and the Markdown content.
			},
		});

		// Listen to StackEdit events and apply the changes.
		stackedit.on("fileChange", async (file) => {
			preview.innerHTML = file.content.html;
			const nodes = preview.querySelectorAll('.katex--display');
			// replace nodes with katex
			for (let i = 0; i < nodes.length; i++) {
				const node = nodes[i];
				const math = node.innerText;
				const html = katex.renderToString(math, {
					displayMode: true,
				});
				node.outerHTML = html;
			}
			dispatch("input", { value: file.content.text });
		});
		// When editor is closed
		stackedit.on("close", async () => {
			dispatch("show-popup");
			// const canvas = await html2canvas(hh);
			// const dataUrl = canvas.toDataURL("image/jpeg");
			// hh.innerHTML = "";
			// gg.appendChild(canvas);
			const html = preview.innerHTML;
			preview.innerHTML = "";
			if (config["on:markdown"]) {
				actions(
					config["on:markdown"],
					{ text: value, html/*, image: dataUrl*/ },
					{
						set: (key, value) => {
							dispatch("set", { key, value });
						},
					}
				);
			}
		});
	}
	function pasteImage(pasteEvent) {
		var item = pasteEvent.clipboardData.items[0];
		if (item.type.indexOf("image") === 0) {
			var blob = item.getAsFile();
			var reader = new FileReader();
			reader.onload = function (event) {
				// paste the image as a base64 string
				document.execCommand(
					"insertText",
					false,
					`![image description](${event.target.result})`
				);
			};
			reader.readAsDataURL(blob);
		}
	}
</script>

<svelte:window on:paste={pasteImage} />

<div class="textarea-markdown">
	<textarea
		class={`${$$restProps.class}`}
		bind:this={textarea}
		bind:value
		on:input
	/>
	<button on:click={open}><i class="fa fa-pen" /></button>
	<div style="position:fixed;top:0px;left:0px;width:420px;" bind:this={preview}></div>
	<div bind:this={gg}></div>
</div>
