<script lang="ts">
	import Abm from "../components/ABM.svelte";
	import * as config from "../../models/questions.json";
	import * as planAnualConfig from "../../models/exams.json";
	import FormFields from "../components/FormFields.svelte";
	import api from "../api";

	export let params = {
		id: null,
	};

	// initialize exam with empty values
	const attrs = Object.keys(planAnualConfig.attributes);
    const form = JSON.parse(JSON.stringify(planAnualConfig));
    form.ui = Object.keys(form.ui).reduce(
        (res, key) => {
            if (form.ui[key].showInUpdate !== false) {
                res[key] = form.ui[key];
            }
            return res;
        },
        {}
    );
	const attributes = attrs.reduce((acc, key) => {
		acc[key] = "";
		return acc;
	}, {});

	let exam = {
		attributes,
	};
	api("exams")
		.get(params.id)
		.then((data) => {
			exam = data;
		});
</script>

<Abm
	{config}
	params={{ params: { exam_id: params.id } }}
	enable_goback={true}
>
	<div slot="header">
		<FormFields
			config={form}
			bind:registro={exam}
			dataTest="exam"
		/>
	</div>
</Abm>
